import { NavLink } from 'react-router-dom';

import './footer.scss';

const Footer = () => (
  <nav className='navFooter'>
    <NavLink
      className='navFooter-link'
      to='/Spinning-Squid/contact'
      activeClassName='navFooter-link--active'
    >
      Nous contacter
    </NavLink>

    <NavLink
      className='navFooter-link'
      to='/Spinning-Squid/newsletter'
      activeClassName='navFooter-link--active'
    >
      Newsletter
    </NavLink>

    <NavLink
      className='navFooter-link'
      to='/Spinning-Squid/spinning-squad'
      activeClassName='navFooter-link--active'
    >
      Spinning Squad
    </NavLink>
  </nav>
);

export default Footer;
