import { NavLink } from 'react-router-dom';
import { slide as Menu } from 'react-burger-menu';
import logo from '../../assets/img/logo1.png';
import avatar from '../../assets/img/logo2.png';

import './header.scss';
import { resetStorage } from '../../generic_functions';

const Header = ({ handleCheckUserUpdateProfile, isLogged, handleLogout }) => (
  <nav className='nav'>
    <div className='left-half-header'>
      <NavLink className='left-header' to='/' exact>
        <img className='nav-logo' src={logo} alt='logo spinning squid' />
      </NavLink>
    </div>

    <div className='right-half-header'>
      <NavLink
        to='/Spinning-Squid'
        exact
        className='nav-link right-header'
        activeClassName='nav-link--active'
      >
        Accueil
      </NavLink>

      <NavLink
        to='/Spinning-Squid/trouve-ton-skatepark'
        className='nav-link right-header'
        activeClassName='nav-link--active'
      >
        Trouve ton Skatepark
      </NavLink>

      <NavLink
        to='/Spinning-Squid/trouve-ton-matos'
        className='nav-link right-header'
        activeClassName='nav-link--active'
      >
        Trouve ton Matos
      </NavLink>

      <NavLink
        to='/Spinning-Squid/communaute'
        className='nav-link right-header'
        activeClassName='nav-link--active'
      >
        Whassup ?!
      </NavLink>

      <NavLink
        to='/Spinning-Squid/contact'
        className='nav-link right-header'
        activeClassName='nav-link--active'
      >
        Question ?
      </NavLink>

      {!isLogged && (
        <NavLink
          to='/Spinning-Squid/connexion'
          exact
          className='nav-link right-header'
          activeClassName='nav-link--active'
        >
          Connexion
        </NavLink>
      )}

      {isLogged && (
        <NavLink
          to='/Spinning-Squid'
          className='nav-link right-header'
          onClick={handleLogout}
        >
          Déconnexion
        </NavLink>
      )}

      {isLogged && (
        <NavLink
          to='/Spinning-Squid/connexion/profil'
          className='right-header'
          activeClassName='nav-link-avatar--active'
          onClick={handleCheckUserUpdateProfile}
        >
          <img className='nav-link-avatar' src={avatar} alt='' />
        </NavLink>
      )}

      {/* Menu burger */}
      <Menu right isOpen={false}>
        <NavLink
          className='nav-link'
          to='/Spinning-Squid'
          exact
          activeClassName='nav-link--active'
        >
          Accueil
        </NavLink>
        <NavLink
          className='nav-link'
          to='/Spinning-Squid/trouve-ton-skatepark'
          activeClassName='nav-link--active'
        >
          Trouve ton Skatepark
        </NavLink>
        <NavLink
          className='nav-link'
          to='/Spinning-Squid/trouve-ton-matos'
          activeClassName='nav-link--active'
        >
          Trouve ton Matos
        </NavLink>
        <NavLink
          className='nav-link'
          to='/Spinning-Squid/communaute'
          activeClassName='nav-link--active'
        >
          Whassup ?!
        </NavLink>
        <NavLink
          className='nav-link'
          to='/Spinning-Squid/contact'
          activeClassName='nav-link--active'
        >
          Question
        </NavLink>
        <NavLink
          className='nav-link'
          to='/Spinning-Squid/connexion'
          activeClassName='nav-link--active'
        >
          Connexion
        </NavLink>
      </Menu>
    </div>
  </nav>
);

export default Header;
